<template>
  <div class="message">


    <div class="width-common">

    <div class="box-line">
      <img src="../../common/images/line-hr.gif"/>
    </div>

    <h2>为了更好的为您服务，请您回答以下问题：</h2>

    <div class="form">
      <div class="box">
        <label>*姓名 </label>
        <input type="text" v-model="formData.userName"/>
      </div>
      <div class="box">
        <label>*联系电话 </label>
        <input type="text" maxlength="11" v-model="formData.mobile"/>
      </div>

      <div class="clearfix"></div>
      <label>*公司名称 </label>
      <input type="text" v-model="formData.companyName"/>
      <label>*设计需求 </label>
      <textarea  v-model="formData.content"></textarea>

      <div class="box-button">
        <button class="reset" @click="reset">重置</button>
        <button class="submit" @click="submit">提交</button>
      </div>


    </div>




    </div>

  </div>
</template>
<script>
  import {PopAlert, ShowLoading, HideLoading} from 'common/js/mylayer'
  import {URL_BASE} from 'common/js/public'
  import {MyPost} from 'common/js/request'
  export default {

    data(){
      return {
        formData:{
          userName:'',
          mobile:'',
          companyName:'',
          content:''
        }

      }
    },
    methods:{
      _submit() { // 初始请求
        MyPost(URL_BASE+'/index.php/api/leaveword/addLeaveword',
          {
            name:this.formData.userName,
            tel:this.formData.mobile,
            company:this.formData.companyName,
            content:this.formData.content,
          }).then(res => {
          console.log(res)
          if (res.return_code === 0) {
            PopAlert('信息提交成功！')
          }else{
            PopAlert(res.return_message)
          }
        });

      },
      submit(){
        console.log(222)
        if(this.formData.userName===''){
          PopAlert('请输入用户名！')
          return false
        }
        if(this.formData.mobile===''){
          PopAlert('请输入联系电话！')
          return false
        }
        if(this.formData.companyName===''){
          PopAlert('请输人公司名称！')
          return false
        }
        if(this.formData.content===''){
          PopAlert('请输人设计需求！')
          return false
        }

        this._submit();
      },
      reset(){
        this.formData.userName ='';
        this.formData.mobile ='';
        this.formData.companyName ='';
        this.formData.content =''
      }
    }
  }



</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>

  .message
    .box-line
      padding-top 170px
      padding-bottom 50px
    h2
      font-size 60px
      padding 40px 0

    .form
      .box
        width 500px
        display inline-block
        margin-right 60px
      label
        font-size 40px
        line-height 55px
        padding 10px 0
        display inline-block
      input
        border 2px solid #ccc
        height 80px
        font-size 30px
        display block
        line-height 1
        width 100%
        box-shadow 0 0 3px 2px #ccc
      textarea
        width 100%
        height 165px
        font-size 30px
        border 2px solid #ccc
        box-shadow 0 0 3px 2px #ccc
      .box-button
        padding 50px 0
        font-size 0
        button
          background-color #c38e00
          text-align center
          cursor pointer
          color #ffffff
          font-size 60px
          border none
          display inline-block
          line-height 100px
          &.reset
            width 420px
            margin-right 50px
          &.submit
            width 730px
</style>





