export const URL_BASE ="http://hyu4299510001.my3w.com";
//获取URL
export function getUrlParam(name) { //获取url上某个参数的值
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    var r = window.location.search.substr(1).match(reg);
    if (r != null) {
        return decodeURIComponent(r[2]);
    }
    return '';
}
//获取COOKIE
export function getCookie(name) {
    var arr = document.cookie.match(new RegExp("(^| )" + name + "=([^;]*)(;|$)"));
    if (arr != null) {
        return unescape(arr[2]);
    } else {
        return null;
    }
}
//页面跳转
export function jumpUrl(url) {
    window.location.href = url;
}
//是否在ie
export function isIE() { //ie?
  if (!!window.ActiveXObject || "ActiveXObject" in window)
    return true;
  else
    return false;
}
//是否在Weixin
export function IsWeixin() {
    var ua = navigator.userAgent.toLowerCase();
    return ua.match(/MicroMessenger/i) == "micromessenger";
}
//是否在Ios
export function IsIos() {
    var ua = navigator.userAgent.toLowerCase();
    return ua.match(/iPhone/i) == "iphone" || ua.match(/iPad/i) == "ipad";
}
//是否在Android
export function IsAndroid() {
    return !IsIos();
}
//是否在APP
export function isApp() {
    var userAgentStr = navigator.userAgent;
    if (userAgentStr.indexOf('zuihuibao') > -1) {
        return true
    } else {
        return false
    }

}
//保留2位小数
export function toDecimal2(x) {
    var f = parseFloat(x);
    if (isNaN(f)) {
        return false;
    }
    var f = Math.floor(x * 100) / 100;
    var s = f.toString();
    var rs = s.indexOf('.');
    if (rs < 0) {
        rs = s.length;
        s += '.';
    }
    while (s.length <= rs + 2) {
        s += '0';
    }
    return s;
}
//版本号
export function userAgenNo() {
    var userAgentStr = navigator.userAgent;
    var userAgenNo = '';
    if (userAgentStr.indexOf('zuihuibao') > -1) {
        userAgenNo = userAgentStr.substr(-5);
    }
    return userAgenNo;
}
//手机号
export function isMobileNum(n) {
    //return /^(?:13\d|15\d|18\d)\d{5}(\d{3}|\*{3})$/.test(n) && parseInt(n)>0;
    return /^\d{11}$/.test(n) && parseInt(n) > 0;
}
//验证码
export function isSmsCode(n) {
    return /^\d{4}$/.test(n);
}

export function AddStorageKV(storage_name, key, val) {
    var p = store.get(storage_name) || {};
    p[key] = val;
    store.set(storage_name, p);
}
//网络监测
export function netStatus() {
    if (window.navigator.onLine != true) {
        alert('请检查网络是否正常！');
        return false;
    }
}










