<template>
  <div class="more-case">
    <h3 v-show = "titleShow">更多案例欣赏 》》》</h3>
    <div class="box-list">
      <div class="width-common">
        <ul>
          <li v-for="item in list" @click="goDetail(item.id)">
            <img :src="URL_BASE + item.img_url"/>
            <p class="title">{{item.product_name}}</p>
            <p class="desc">产品描述</p>
          </li>
        </ul>

        <div class="clearfix"></div>

        <div class="pages" v-show="data_pages.length > 1">
          <span :class="{'active': index == currentPage }" v-for="(page,index) in data_pages" @click="getList(page-1)">{{page}}</span>
        </div>
      </div>
    </div>



  </div>
</template>
<script>
  import {PopAlert, ShowLoading, HideLoading} from 'common/js/mylayer'
  import {URL_BASE} from 'common/js/public'
  import {MyPost,MyGet} from 'common/js/request'
  export default {

    data(){
      return{
        list:[],
        URL_BASE:URL_BASE,
        data_pages:[],
        limit:5,
        currentPage:0
      }
    },
    props: {
      titleShow: {
        type: Boolean,
        default: true
      },
    },
    created(){
      this.getList(this.currentPage)
    },
    methods:{
      goDetail(id){
        console.log(window.location.href.indexOf('detail') > -1)
        if(window.location.href.indexOf('detail') > -1){
          location.reload()
          this.$router.push({
            path:'/detail',
            query:{
              id:id
            }
          })
          document.documentElement.scrollTop = document.body.scrollTop =0;
        }else{
          this.$router.push({
            path:'/detail',
            query:{
              id:id
            }
          })
          document.documentElement.scrollTop = document.body.scrollTop =0;
        }

      },
      getList(currentPage) { // 初始请求

        this.currentPage = currentPage;

        MyPost(URL_BASE+'/index.php/api/product/getlist',
          {
            index:currentPage,
            limit:this.limit,
            device:'pc'
          }).then(res => {
          console.log(res)
          if (res.return_code == 0) {
            this.list = res.data.list;
            this.data_pages =[];
            var cnt = res.data.cnt;
            var PageCount=Math.ceil(cnt/this.limit);
            for(var i=1;i<=PageCount;i++){
              this.data_pages.push(i)
            }
            console.log(this.data_pages)

          }else{
            PopAlert(res.return_message)
          }
        });

      },
    }

  }

</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
.more-case
  width 1200px
  margin  0 auto
  h3
    line-height 60px
    font-size 40px
  .box-list
    background-color #ffffff
    overflow hidden
    ul
      padding 20px 0
      li
        width 283px
        float left
        margin-right 10px
        margin-bottom 70px
        cursor pointer
        text-align left
        img
          width 283px
          height 260px
        .title
          margin-top 20px
          font-size 22px
          line-height 28px
          color #282828
        .desc
          font-size 18px
          line-height 24px
          color #505050
    .pages
      padding 20px 0
      text-align center
      span
        width 20px
        font-size 14px
        line-height 20px
        text-align cursor
        color #999
        height 20px
        margin 0 10px
        display inline-block
        cursor pointer
        &.active
          background-color #000000
          color #fff
</style>





